import React, { useState } from 'react';
import auth0 from 'auth0-js';
import { LoginPage } from './pages/login';
import { LoginRequest } from './@types';

function App() {
  const [error, setError] = useState<string>();

  let webAuth: auth0.WebAuth | undefined = undefined;

  const config = (window as any)?.__config__;
  if (config) {
    const title = config.dict?.signin?.title;
    document.title = `Log in | ${title}`;
    const leeway = config.internalOptions.leeway;
    if (leeway) {
      const convertedLeeway = parseInt(leeway);

      if (!isNaN(convertedLeeway)) {
        config.internalOptions.leeway = convertedLeeway;
      }
    }

    const params = Object.assign(
      {
        overrides: {
          __tenant: config.auth0Tenant,
          __token_issuer: config.authorizationServer.issuer,
        },
        domain: config.auth0Domain,
        clientID: config.clientID,
        redirectUri: config.callbackURL,
        responseType: 'token id_token',
      },
      config.internalOptions
    );

    webAuth = new auth0.WebAuth(params);
  }

  const handleLogin = (data: LoginRequest) => {
    const databaseConnection = 'Username-Password-Authentication';
    webAuth?.login(
      {
        realm: databaseConnection,
        email: data.email,
        password: data.password,
      },
      error => {
        if (error) handleError(error);
      }
    );
  };
  const socialLogin = (social: 'facebook' | 'google' | 'apple') => {
    let connection;
    switch (social) {
      case 'google':
        connection = 'google-oauth2';
        break;
      case 'facebook':
        connection = 'facebook';
        break;
      case 'apple':
        connection = 'apple';
        break;
      default:
        break;
    }

    webAuth?.authorize({
      connection,
      domain: config.auth0Domain,
      clientID: config.clientID,
      redirectUri: config.callbackURL,
      responseType: 'token id_token',
    });
  };

  const handleError = (error?: auth0.Auth0Error) => {
    setError(error?.description || error?.policy);
  };
  return <LoginPage errorMessage={error} onLogin={handleLogin} socialLogin={socialLogin} />;
}

export default App;
