import { REGEXP_PASSWORD } from './constants';

export const validatePassword = (password: string) => {
  const minLength = 8; // Minimum password length
  const minLowercase = 1; // Minimum lowercase letters
  const minUppercase = 1; // Minimum uppercase letters
  const minNumbers = 1; // Minimum numbers
  const minSpecialChars = 1; // Minimum special characters
  const specialCharsRegex = REGEXP_PASSWORD; // Special characters regex

  if (password.length < minLength) {
    return 'Password must be at least 8 characters long';
  }

  if (password.replace(/[a-z]/g, '').length < minLowercase) {
    return 'Password must contain at least 1 lowercase letter';
  }

  if (password.replace(/[A-Z]/g, '').length < minUppercase) {
    return 'Password must contain at least 1 uppercase letter';
  }

  if (password.replace(/[0-9]/g, '').length < minNumbers) {
    return 'Password must contain at least 1 number';
  }

  if (!specialCharsRegex.test(password) || password.replace(specialCharsRegex, '').length < minSpecialChars) {
    return 'Password must contain at least 1 special character';
  }

  return true;
};
