import React, { useState } from 'react';
import { Box, Grid, IconButton, InputAdornment, Stack, Typography } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import useMediaQuery from '@/services/useMediaQuery';
import { useForm } from 'react-hook-form';
import TextInputController from '@/components/TextField/TextInputController/TextInputController';
import { RequiredAsterisk } from '@/components/RequiredAsterisk';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { LoginRequest } from '@/@types';
import { validatePassword } from '@/helpers/ultis';

interface LoginFormProps {
  onLogin: (data: LoginRequest) => void;
}

const LoginForm = ({ onLogin }: LoginFormProps) => {
  const { isMobile } = useMediaQuery();

  const [showPassword, setShowPassword] = useState(false);
  const handleTogglePassword = () => {
    setShowPassword(prevShowPassword => !prevShowPassword);
  };

  const { control, handleSubmit } = useForm<LoginRequest>({
    defaultValues: {
      email: '',
      password: '',
    },
  });

  const onSubmit = (data: LoginRequest) => {
    onLogin(data);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Stack gap={6} direction="column">
        <Box display="flex" gap={2} flexDirection="column">
          <Typography variant={isMobile ? 'h4' : 'h3'}>Sign in</Typography>
          <Stack direction="row" gap={2}>
            <Typography variant="body2">New to PQ?</Typography>
            <Typography
              variant="body2"
              color="system.link"
              sx={{
                cursor: 'pointer',
              }}
            >
              Sign up
            </Typography>
          </Stack>
        </Box>
        <TextInputController
          control={control}
          name="email"
          size="small"
          label="Email"
          required
          fullWidth
          rules={{
            email: true,
          }}
        />
        <div>
          <Grid container justifyContent="space-between">
            <Grid item>
              <Typography variant="label2" color="system.sub">
                Password <RequiredAsterisk />
              </Typography>
            </Grid>
            {isMobile && (
              <Grid item>
                <Typography
                  variant="label2"
                  color="system.link"
                  sx={{
                    cursor: 'pointer',
                  }}
                >
                  Forgot password
                </Typography>
              </Grid>
            )}
          </Grid>
          <TextInputController
            control={control}
            name="password"
            label="Password"
            hideLabel
            size="small"
            required
            fullWidth
            type={showPassword ? 'text' : 'password'}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton onClick={handleTogglePassword}>
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
            rules={{
              validate: validatePassword,
            }}
          />
        </div>
        <LoadingButton
          variant="contained"
          sx={{
            padding: 3,
          }}
          type="submit"
        >
          <Typography variant="body2">Sign in</Typography>
        </LoadingButton>
      </Stack>
    </form>
  );
};

export default LoginForm;
